/*
 * author: 张浩
 * createdTime: 2019/07/19
 * */

import { Bread } from 'Bread'
import React, { PureComponent } from 'react'
import style from './components/style/Style.scss'
import Tool from './components/./Tool'
import OperationModal from './components/OperationModal'
import OperationModalFund from './components/OperationModalFund'
import { connect } from 'react-redux'
import { mapDispatchToProps } from './redux/mapDispatchToProps'
import {
  API_DIFFAPPORTIONMENTS_COMPANY_LIST,
  API_DIFFAPPORTIONMENT_DATAS_DIFF_MAINMATERIAL,
  API_DIFFAPPORTIONMENT_DATAS_DIFF_PACKAGEMATERIAL,
  API_DIFFAPPORTIONMENT_DATAS_DIFF_SUPPORTMATERIAL,
  API_DIFFAPPORTIONMENT_DATAS_FUND_SUPPORTMATERIAL,
  API_DIFFAPPORTIONMENT_DATAS_FUND_WORKACCOUNT,
  API_DIFFAPPORTIONMENT_DATAS_FUND_MACHINEACCOUNT
} from 'api'
import { getUrlDataObj, countTableScroll } from 'util'
import { Form, Row, Col, Table, DatePicker, Spin } from 'antd'
import { CsxFormSelect } from 'Form'
const FormItem = Form.Item
import moment from 'moment'
@connect(
  (state) => ({
    ...state.diffApportionmentData,
    menuPermissionIds: state.menuPermissionIds
  }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      editOrCreate: '', //  create -- 新建      edit -- 详情、 编辑
      flag_apportionmentInTable: '', // 差异分摊 : allowApportionment, forbiddenApportionment
      flag_postTable: '', // 差异过账 : allowPost, forbiddenPost
      flag_checkData: '', // 查看数据 : allowCheckData, forbiddenCheckData
      stepId: '',
      hasGetSteps: false //  是否已经获取步骤
    }
  }
  componentDidMount() {
    try {
      const { location = {}, update, handleGetListDet } = this.props
      const { search = '' } = location
      const {
        companyCode = '',
        period = '',
        locationUrl = '',
        companyName = ''
      } = getUrlDataObj(search || '')
      // 获取地点级别数据
      handleGetListDet(
        { searchParams: { companyCode, period, dataRange: 'location' } },
        (arrList) => {
          update({
            loadingInit: false
          })
        }
      )

      let companyObj = {
        value: decodeURIComponent(companyCode),
        children: `${decodeURIComponent(`${companyCode}`)} ${decodeURIComponent(
          `${companyName}`
        )}`
      }
      update({
        searchParams: {
          companyCode,
          companyName,
          locationUrl: decodeURIComponent(locationUrl),
          company: companyObj,
          period: moment(period).format('YYYY-MM'),
          initDate: moment(period)
        },
        toolDataObj: {
          strTool: '公司代码',
          codeShowTool: companyCode,
          strShow: '地点编码'
        },
        dataRange: 'location'
      })
    } catch (error) {
      console.error(error)
    }
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const { location, userLoation, handleGetListDet } = this.props
    const { search = '' } = location
    const { companyCode = '', period = '' } = getUrlDataObj(search || '')
    const newUserLoationValue =
      (newProps && newProps.userLoation && newProps.userLoation.value) || ''
    const userLoationValue = (userLoation && userLoation.value) || ''
    if (newUserLoationValue != userLoationValue) {
      handleGetListDet(
        { searchParams: { companyCode, period, dataRange: 'location' } },
        (arrList) => { }
      )
    }
  }
  componentWillUnmount() {
    const { update } = this.props
    update({ searchResult: [], searchParams: {} })
  }
  handleSelectChange = () => { }
  // 改变当前的输入值
  onInputChange(values) {
    const { searchParams, update } = this.props
    update({ searchParams: { ...searchParams, ...values } })
  }
  renderFormItem = (searchParams) => {
    const {
      form: { getFieldDecorator }
    } = this.props
    const { initDate, company = '', companyCode } = searchParams
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 10 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 14 }
      }
    }
    return (
      <div className={style.form_item_det}>
        <Row type="flex" justify="start">
          <Col span={8} style={{ height: '44px' }}>
            <FormItem {...formItemLayout} label="公司" hasFeedback>
              {getFieldDecorator('company', {
                initialValue: company,
                rules: [
                  {
                    required: true,
                    message: '请填写!'
                  }
                ]
              })(
                <CsxFormSelect
                  disabled={true}
                  allowClear={true}
                  dateApi={API_DIFFAPPORTIONMENTS_COMPANY_LIST}
                  selectItem={{
                    data: companyCode,
                    key: 'code',
                    valueKey: ['code', 'name']
                  }}
                  handleSelectChange={(data) => {
                    const { value = '', children = '' } = data
                    this.onInputChange({
                      companyCode: value,
                      companyName:
                        children &&
                        children.length > 0 &&
                        children.split(' ')[1],
                      company: `${children}`
                    })
                  }}
                />
              )}
            </FormItem>
          </Col>
          <Col span={8} style={{ height: '44px' }}>
            <div className="commonInput">
              <div className={style.dataPickerDet}>
                <label>期间：</label>
                <div className="box" style={{ marginRight: 0 }}>
                  <DatePicker
                    disabled={true}
                    onChange={(time) => {
                      let period = time && `${moment(time).format('YYYY-MM')}`
                      this.onInputChange({
                        period,
                        initDate: time
                      })
                    }}
                    format={'YYYY-MM'}
                    value={initDate}
                  />
                </div>
              </div>
            </div>
          </Col>
        </Row>
      </div>
    )
  }
  renderFormItemNext = () => (
    <div className={style.form_item_table}>{this.renderFormItemInput()}</div>
  )
  renderFormItemInput = () => {
    const {
      flag_apportionmentInTable,
      flag_postTable,
      flag_checkData
    } = this.state
    return (
      <Row type="flex" justify="start">
        <Col span={24}>
          <Tool
            history={this.props.history}
            flag_apportionmentInTable={flag_apportionmentInTable}
            flag_postTable={flag_postTable}
            flag_checkData={flag_checkData}
            editOrCreate={this.state.editOrCreate}
            hasGetSteps={this.state.hasGetSteps}
          />
        </Col>
      </Row>
    )
  }
  linkToDet = (codeData) => {
    const {
      update,
      handleGetListDet,
      searchParams,
      dataRange,
      toolDataObj = {}
    } = this.props
    const { companyCode, period } = searchParams
    const { locationCode } = toolDataObj
    update({
      loadingInit: true,
      searchResult: []
    })
    if (dataRange === 'location') {
      handleGetListDet(
        {
          searchParams: {
            companyCode,
            period,
            locationCode: codeData,
            dataRange: 'costCenter'
          }
        },
        (arrList) => {
          update({
            loadingInit: false
          })
        }
      )
      // 地点级别
      update({
        dataRange: 'costCenter',
        toolDataObj: {
          locationCode: codeData,
          strTool: '地点编码',
          codeShowTool: codeData,
          strShow: '成本中心编码'
        }
      })
    } else if (dataRange === 'costCenter') {
      // 成本中心级别 跳到工单级别
      handleGetListDet(
        {
          searchParams: {
            page: 1,
            size: 10,
            companyCode,
            period,
            costCenterCode: codeData,
            dataRange: 'order'
          }
        },
        (arrList) => {
          update({
            loadingInit: false
          })
        }
      )
      update({
        dataRange: 'order',
        toolDataObj: {
          ...toolDataObj,
          strTool: '地点编码',
          codeShowTool: locationCode,
          strToolCost: '成本中心编码',
          codeShowToolCost: codeData,
          costCenterCode: codeData,
          strShow: ''
        }
      })
    }
  }
  onChange = (pagination, filters, sorter, extra) => {
    const {
      handleGetListDet,
      searchParams,
      page,
      size,
      toolDataObj
    } = this.props
    const { companyCode, period } = searchParams
    const { costCenterCode } = toolDataObj
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetListDet(
          {
            searchParams: {
              companyCode,
              period,
              costCenterCode,
              dataRange: 'order',
              page,
              size
            }
          },
          (arrList) => { }
        )
      } else {
        handleGetListDet({
          searchParams: {
            page,
            size,
            costCenterCode,
            period,
            companyCode,
            dataRange: 'order',
            isAsc: false,
            columns: sorter.field
          }
        })
      }
    } else {
      // 判断是点跳页面还是排序
      let reqObj = { ...searchParams }
      for (let i in reqObj) {
        if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
          reqObj[i] = reqObj[i].value
        }
      }
      handleGetListDet({
        searchParams: {
          page: pagination.current,
          size: pagination.pageSize,
          costCenterCode,
          period,
          companyCode,
          dataRange: 'order'
        }
      })
    }
  }
  onEdit(text, record, index, type, dataObj) {
    const { handleGetListFund, searchParams, toolDataObj, update } = this.props
    const { companyCode, period } = searchParams
    const { locationCode = '' } = toolDataObj
    const { costCenterCode = '' } = dataObj
    let apiUrl = '',
      modalTitle = ''
    if (type === 'final') {
      // F金额
      switch (index) {
        case 2:
          apiUrl = API_DIFFAPPORTIONMENT_DATAS_FUND_SUPPORTMATERIAL
          modalTitle = `${costCenterCode}辅材-F实际`
          break
        case 3:
          apiUrl = API_DIFFAPPORTIONMENT_DATAS_FUND_MACHINEACCOUNT
          modalTitle = `${costCenterCode}机时-F实际`
          break
        case 4:
          apiUrl = API_DIFFAPPORTIONMENT_DATAS_FUND_WORKACCOUNT
          modalTitle = `${costCenterCode}工时-F实际`
          break
        default:
      }
    } else {
      // 量差
      switch (index) {
        case 0:
          apiUrl = API_DIFFAPPORTIONMENT_DATAS_DIFF_MAINMATERIAL
          modalTitle = `${costCenterCode}主材-量差`
          break
        case 1:
          apiUrl = API_DIFFAPPORTIONMENT_DATAS_DIFF_PACKAGEMATERIAL
          modalTitle = `${costCenterCode}包材-量差`
          break
        case 2:
          apiUrl = API_DIFFAPPORTIONMENT_DATAS_DIFF_SUPPORTMATERIAL
          modalTitle = `${costCenterCode}辅材-量差`
          break
        default:
      }
    }
    update({ isShowModalFund: true, modalTitle })
    handleGetListFund({
      searchParams: { companyCode, period, costCenterCode, locationCode },
      apiUrl
    })
  }
  renderBtn(text, record, index, type, dataObj) {
    let dom = (
      <span
        className={'blue'}
        onClick={() => {
          this.onEdit(text, record, index, type, dataObj)
        }}
      >
        {text}
      </span>
    )
    return dom
  }
  renderStrAlert = (str) => {
    let domAlert = <span className={style.alert_in_table}>{str}</span>
    return domAlert
  }
  renderFormItemTable = (dataObj) => {
    const {
      tableLoading,
      searchResult = [],
      dataRange,
      toolDataObj = {},
      total,
      page,
      size
    } = this.props
    let { strShow } = toolDataObj
    const {
      list = [],
      isProd = false,
      locationCode = '',
      costCenterCode = '',
      orderCode = ''
    } = dataObj || {}
    let codeShow = locationCode
    let dataSource = []
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['10', '50', '100', '200', '500']
    }
    console.log('dataRange is ', dataRange)
    let columns = [
      {
        title: (text, record) => (
          <div
            style={{
              float: 'left',
              textAlign: 'left',
              height: '30px',
              cursor: 'pointer',
              lineHeight: '30px'
            }}
            // 成本中心层级页面 (dataRange  : string) - 非产线成本中心的话(isProd :bool) 页面不能往下一层级跳转
            onClick={
              !isProd && dataRange === 'costCenter'
                ? null
                : this.linkToDet.bind(this, codeShow)
            }
          >
            <p className={style.title_in_table}>{strShow}</p>
            <span
              className={
                !isProd && dataRange === 'costCenter' ? '' : style.link_in_table
              }
            >
              {codeShow}
            </span>
            <span>
              {/* 地点层级页面 - 非正常地点编码成本中心
                    成本中心层级页面 - 非产线成本中心
                */}
              {!isProd
                ? dataRange === 'costCenter'
                  ? this.renderStrAlert('(非产线成本中心！)')
                  : this.renderStrAlert('(非正常地点编码！)')
                : null}
            </span>
          </div>
        ),
        width: 100,
        dataIndex: 'final',
        key: 'final',
        children: [
          {
            title: '项目',
            dataIndex: 'projectName',
            key: 'projectName',
            width: 150
          },
          {
            title: 'F-实际',
            dataIndex: 'final',
            key: 'final',
            width: 150,
            render: (text, record, index) => {
              let content =
                dataRange === 'costCenter' && index > 1 && index < 5 ? (
                  <div className="tableOperation">
                    {this.renderBtn(text, record, index, 'final', dataObj)}
                  </div>
                ) : (
                    text
                  )
              return content
            }
          },
          {
            title: 'P-计划',
            dataIndex: 'plan',
            key: 'plan',
            width: 150
          },
          {
            title: 'D-调整',
            dataIndex: 'decude',
            key: 'decude',
            width: 150
          }
        ]
      },
      {
        title: '调整明细项',
        width: 100,
        children: [
          {
            title: '包材税金转出',
            dataIndex: 'taxOut',
            key: 'taxOut',
            width: 150
          },
          {
            title: '波次退料差异',
            dataIndex: 'batchRejectedDiff',
            key: 'batchRejectedDiff',
            width: 150
          },
          {
            title: '量差（有工单）',
            dataIndex: 'hasOrder',
            key: 'hasOrder',
            width: 150
          }
        ]
      },
      {
        title: '非调整项',
        width: 100,
        children: [
          {
            title: '无工单',
            dataIndex: 'noOrder',
            key: 'noOrder',
            width: 150
          },
          {
            title: '总量差',
            dataIndex: 'diff',
            key: 'diff',
            width: 150,
            render: (text, record, index) => {
              let content =
                dataRange === 'costCenter' && index < 3 ? (
                  <div className="tableOperation">
                    {this.renderBtn(text, record, index, 'diff', dataObj)}
                  </div>
                ) : (
                    text
                  )
              return content
            }
          }
        ]
      }
    ]

    if (dataRange === 'location') {
      // 地点级别
      codeShow = locationCode
      dataSource = list
    } else if (dataRange === 'costCenter') {
      // 成本中心级别
      codeShow = costCenterCode
      dataSource = list
    } else if (dataRange === 'order') {
      // 工单级别
      codeShow = orderCode
      dataSource = searchResult
      columns = [
        {
          title: '',
          width: 400,
          children: [
            {
              title: '生产工单',
              dataIndex: 'orderCode',
              key: 'orderCode',
              width: 200
            },
            {
              title: '成品',
              dataIndex: 'productCode',
              key: 'productCode',
              width: 200,
              render: (text, record) => `${text} - ${record.productName}`
            }
          ]
        },
        {
          title: '成本小计',
          width: 300,
          children: [
            {
              title: 'F-实际',
              dataIndex: 'ftotalCost',
              key: 'ftotalCost',
              width: 100
            },
            {
              title: 'P-计划',
              dataIndex: 'ptotalCost',
              key: 'ptotalCost',
              width: 100
            },
            {
              title: 'D-调整',
              dataIndex: 'dtotalCost',
              key: 'dtotalCost',
              width: 100
            }
          ]
        },
        {
          title: '主材',
          width: 400,
          children: [
            {
              title: 'F-实际',
              dataIndex: 'fmainMaterialCost',
              key: 'fmainMaterialCost',
              width: 100
            },
            {
              title: 'P-计划',
              dataIndex: 'pmainMaterialCost',
              key: 'pmainMaterialCost',
              width: 100
            },
            {
              title: 'D-调整',
              dataIndex: 'dmainMaterialCost',
              key: 'dmainMaterialCost',
              width: 100
            },
            {
              title: '主材波次退料差异',
              dataIndex: 'dmainMaterialBatch',
              key: 'dmainMaterialBatch',
              width: 120
            },
            {
              title: '主材量差',
              dataIndex: 'dmainMaterialQty',
              key: 'dmainMaterialQty',
              width: 100
            }
          ]
        },
        {
          title: '包材',
          width: 400,
          children: [
            {
              title: 'F-实际',
              dataIndex: 'fpackageMaterialCost',
              key: 'fpackageMaterialCost',
              width: 100
            },
            {
              title: 'P-计划',
              dataIndex: 'ppackageMaterialCost',
              key: 'ppackageMaterialCost',
              width: 100
            },
            {
              title: 'D-调整',
              dataIndex: 'dpackageMaterialCost',
              key: 'dpackageMaterialCost',
              width: 100
            },
            {
              title: '包材波次退料差异',
              dataIndex: 'dpackageMaterialBatch',
              key: 'dackageMaterialBatch',
              width: 120
            },
            {
              title: '包材税金转出',
              dataIndex: 'dpackageMaterialTaxAmount',
              key: 'dpackageMaterialTaxAmount',
              width: 120
            },
            {
              title: '包材量差',
              dataIndex: 'dpackageMaterialQty',
              key: 'dpackageMaterialQty',
              width: 100
            }
          ]
        },
        {
          title: '辅材',
          width: 400,
          children: [
            {
              title: 'F-实际',
              dataIndex: 'fsupportMaterialCost',
              key: 'fsupportMaterialCost',
              width: 100
            },
            {
              title: 'P-计划',
              dataIndex: 'psupportMaterialCost',
              key: 'psupportMaterialCost',
              width: 100
            },
            {
              title: 'D-调整',
              dataIndex: 'dsupportMaterialCost',
              key: 'dsupportMaterialCost',
              width: 100
            },
            {
              title: '辅材量差',
              dataIndex: 'dsupportMaterialQty',
              key: 'dsupportMaterialQty',
              width: 100
            }
          ]
        },
        {
          title: '工时',
          width: 300,
          children: [
            {
              title: 'F-实际',
              dataIndex: 'fworkFeeCost',
              key: 'fworkFeeCost',
              width: 100
            },
            {
              title: 'P-计划',
              dataIndex: 'pworkFeeCost',
              key: 'pworkFeeCost',
              width: 100
            },
            {
              title: 'D-调整',
              dataIndex: 'dworkFeeCost',
              key: 'dworkFeeCost',
              width: 100
            }
          ]
        },
        {
          title: '机时',
          width: 300,
          children: [
            {
              title: 'F-实际',
              dataIndex: 'fmachineFeeCost',
              key: 'fmachineFeeCost',
              width: 100
            },
            {
              title: 'P-计划',
              dataIndex: 'pmachineFeeCost',
              key: 'pmachineFeeCost',
              width: 100
            },
            {
              title: 'D-调整',
              dataIndex: 'dmachineFeeCost',
              key: 'dmachineFeeCost',
              width: 100
            }
          ]
        }
      ]
    }
    return (
      <Row>
        <div className="table-group table_detail">
          <div className="table-group">
            {dataSource && dataSource.length ? (
              <Table
                rowKey={(record) =>
                  dataRange === 'location'
                    ? record.projectName
                    : dataRange === 'costCenter'
                      ? record.projectName
                      : dataRange === 'order'
                        ? record.orderCode
                        : ''
                }
                columns={columns}
                bordered={true}
                dataSource={dataSource || []}
                scroll={{ x: countTableScroll(columns), y: 291 }}
                onChange={this.onChange}
                pagination={dataRange === 'order' ? pagination : false}
              />
            ) : null}
          </div>
        </div>
      </Row>
    )
  }
  render() {
    const {
      searchResult = [],
      searchParams = {},
      dataRange,
      tableLoading = true
    } = this.props
    const { loadingInit = false } = this.props
    console.log('loadingInit is', loadingInit)
    return (
      <Spin tip="数据加载中..." spinning={loadingInit}>
        <section>
          <Bread route={['成本中心', '月底差异分摊', '查看数据']} />
          <Form autoComplete="off">{this.renderFormItem(searchParams)}</Form>
          <div className={style.form_item_det}>
            <div>{this.renderFormItemNext()}</div>
            {dataRange === 'order' ? (
              this.renderFormItemTable()
            ) : searchResult && searchResult.length ? (
              searchResult.map((item) => this.renderFormItemTable(item))
            ) : (
                  <div></div>
                )}
          </div>

          {this.props.isShowModal ? <OperationModal /> : null}
          {this.props.isShowModalFund ? <OperationModalFund /> : null}
        </section>
      </Spin>
    )
  }
}
export default Form.create()(App)



