import React, { PureComponent } from 'react'
import { Modal, Row } from 'antd'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import style from './style/Tool.scss'
@connect(
  state => ({ ...state.diffApportionmentData }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  componentDidMount() {
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const { update } = this.props
    update({ isShowModal: false })
  }
  render() {
    const {
      modalTitle
    } = this.props

    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        maskClosable={false}
        className='operationModal'
        footer={null}
        width='900px'
      >

          <Row gutter={16}>
            <div >
              <img className = {style.img} src='/image/diffTips.jpeg' alt=" 月底差异使用说明"/>
            </div>
          </Row>

      </Modal>
    )
  }
}
export default (App)



