// 导出数据报表
export const handleExport = (reqData, callBack) => (dispatch) => {
  dispatch({
    type: actionType.DIFFAPPORTIONMENT_DATA_UPDATE,
    data: { modalTuttonLoading: true }
  })
  const { dataRange = '', ...searchData } = reqData
  let apiUrl =
    dataRange === 'location'
      ? API_DIFFAPPORTIONMENT_DATAS_EXPORT_LOCATION
      : dataRange === 'costCenter'
      ? API_DIFFAPPORTIONMENT_DATAS_STEP_COSTCENTER
      : dataRange === 'order'
      ? API_DIFFAPPORTIONMENT_DATAS_EXPORT_ORDER
      : ''
  getData({ api: apiUrl, ...searchData })
    .then((data) => {
      callBack && callBack()
      dispatch({
        type: actionType.DIFFAPPORTIONMENT_DATA_UPDATE,
        data: { modalTuttonLoading: false }
      })
    })
    .catch((err) => {
      dispatch({
        type: actionType.DIFFAPPORTIONMENT_DATA_FAIL
      })
    })
}
// 导出全部数据报表
export const handleExportAll = (reqData, callBack) => (dispatch) => {
  dispatch({
    type: actionType.DIFFAPPORTIONMENT_DATA_UPDATE,
    data: { modalTuttonLoadingAll: true }
  })
  const { dataRange = '', ...searchData } = reqData
  let apiUrl =
    dataRange === 'location'
      ? API_DIFFAPPORTIONMENT_DATAS_EXPORT_LOCATION
      : dataRange === 'costCenter'
      ? API_DIFFAPPORTIONMENT_DATAS_STEP_COSTCENTER
      : dataRange === 'order'
      ? API_DIFFAPPORTIONMENT_DATAS_EXPORT_ORDER
      : ''
  getData({ api: apiUrl, ...searchData })
    .then((data) => {
      callBack && callBack()
      dispatch({
        type: actionType.DIFFAPPORTIONMENT_DATA_UPDATE,
        data: { modalTuttonLoadingAll: false }
      })
    })
    .catch((err) => {
      dispatch({
        type: actionType.DIFFAPPORTIONMENT_DATA_FAIL
      })
    })
}
// 获取财务展示列表
export const handleGetListFund = (reqData, callBack) => (dispatch) => {
  const { apiUrl, ...searchData } = reqData
  update({
    tableLoading: true
  })
  getData({ api: apiUrl, ...searchData })
    .then((data) => {
      callBack && callBack()
      const dataResult = (data && data.result && data.result.balances) || []
      dispatch({
        type: actionType.DIFFAPPORTIONMENT_DATA_UPDATE,
        data: {
          fundLIstResult: dataResult,
          tableLoading: false
        }
      })
    })
    .catch((err) => {
      dispatch({
        type: actionType.DIFFAPPORTIONMENT_DATA_FAIL
      })
    })
}



