import React, { PureComponent } from 'react'
import { Button, Row, Col, message } from 'antd'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { withRouter } from 'react-router'
@connect((state) => ({ ...state.diffApportionmentData }), mapDispatchToProps)
class TableTool extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  // 返回上一级
  handleCheckData = (dataRange) => {
    const {
      history,
      update,
      searchParams,
      handleGetListDet,
      toolDataObj = {}
    } = this.props
    const { locationUrl } = searchParams
    const { companyCode, period } = searchParams
    const { locationCode } = toolDataObj
    update({
      loadingInit: true
    })
    if (dataRange === 'location') {
      // 地点级别
      update({
        dataRange: '',
        toolDataObj: {}
      })
      history.push(locationUrl)
      return false
    } else if (dataRange === 'costCenter') {
      // 成本中心级别
      handleGetListDet(
        { searchParams: { companyCode, period, dataRange: 'location' } },
        (arrList) => {
          update({
            loadingInit: false
          })
        }
      )
      // 地点级别
      update({
        dataRange: 'location',
        toolDataObj: {
          ...toolDataObj,
          strTool: '公司代码',
          codeShowTool: companyCode,
          strShow: '地点编码'
        }
      })
    } else if (dataRange === 'order') {
      // 成本中心级别
      handleGetListDet(
        {
          searchParams: {
            companyCode,
            period,
            locationCode,
            dataRange: 'costCenter'
          }
        },
        (arrList) => {
          update({
            loadingInit: false
          })
        }
      )
      // 地点级别
      update({
        dataRange: 'costCenter',
        toolDataObj: {
          ...toolDataObj,
          strTool: '地点编码',
          codeShowTool: locationCode,
          strShow: '成本中心编码'
        }
      })
    }
  }
  // 导出
  handleExport = () => {
    const { handleExport, searchParams, dataRange, toolDataObj } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    const { companyCode = '', period = '' } = reqObj
    const { locationCode = '', costCenterCode = '' } = toolDataObj
    let paramData =
      dataRange === 'location'
        ? { companyCode, period }
        : dataRange === 'costCenter'
        ? { companyCode, period, locationCode }
        : dataRange === 'order'
        ? { companyCode, period, costCenterCode }
        : ''
    handleExport({ searchParams: { ...paramData }, dataRange }, () => {
      message.success('导出成功')
    })
  }
  // 导出地点下所有的明细
  handleExportAll = () => {
    const { handleExport, searchParams, dataRange, toolDataObj } = this.props
    let reqObj = { ...searchParams }
    alert('导出所有')
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    const { companyCode = '', period = '' } = reqObj
    const { locationCode = '', costCenterCode = '' } = toolDataObj
    let paramData =
      dataRange === 'location'
        ? { companyCode, period }
        : dataRange === 'costCenter'
        ? { companyCode, period, locationCode }
        : dataRange === 'order'
        ? { companyCode, period, costCenterCode }
        : ''
    handleExport({ searchParams: { ...paramData }, dataRange }, () => {
      message.success('导出成功')
    })
  }
  // 新增
  handleTipShow = () => {
    const { update } = this.props
    update({
      isShowModal: true,
      modalTitle: '月底差异分摊报表使用说明',
      modalData: ''
    })
  }
  render() {
    const {
      toolDataObj = {},
      dataRange,
      modalTuttonLoading,
      modalTuttonLoadingAll
    } = this.props // dataRange--  location: 地点级别 ， costCenter: 成本中心级别 ， order: 工单级别
    const { strTool, codeShowTool, strToolCost, codeShowToolCost } = toolDataObj
    return (
      <div className={style['table-tool']}>
        <Row type="flex" justify="start">
          <Col span={5}>
            <div className={style['table-tool-text']}>{strTool}</div>
            <div className={style['table-tool-text']}>{codeShowTool}</div>
          </Col>
          <Col span={5}>
            {dataRange === 'order' ? (
              <span>
                <div className={style['table-tool-text']}>{strToolCost}</div>
                <div className={style['table-tool-text']}>
                  {codeShowToolCost}
                </div>
              </span>
            ) : null}
          </Col>
          <Col span={8} push={6}>
            <Button
              type="primary"
              className={style.btn_right}
              onClick={this.handleCheckData.bind(this, dataRange)}
            >
              返回上一级
            </Button>

            <Button
              type="primary"
              className={style.btn_right}
              onClick={this.handleExport}
              loading={modalTuttonLoading}
            >
              报表导出
            </Button>
            {/* <Button
              type="primary"
              className={style.btn_right}
              onClick={this.handleExportAll}
              loading={modalTuttonLoadingAll}
            >
              全部导出
            </Button> */}
            {dataRange === 'location' ? (
              <Button
                type="primary"
                className={style.btn_right}
                onClick={this.handleTipShow}
              >
                报表说明
              </Button>
            ) : null}
          </Col>
        </Row>
      </div>
    )
  }
}
export default withRouter(TableTool)



