import React, { PureComponent } from 'react'
import { Modal, Row, Table } from 'antd'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import style from './style/Tool.scss'
import { countTableScroll } from 'util'
@connect(
  state => ({ ...state.diffApportionmentData }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  componentDidMount() {}
  // 关闭弹窗
  handleCloseModal = () => {
    const { update } = this.props
    update({ isShowModalFund: false, fundLIstResult: [] })
  }
  renderBomData() {
    const { fundLIstResult = [], tableLoading } = this.props
    console.log(' fundLIstResult is ', fundLIstResult)
    const columns = [
      {
        title: '科目编码',
        width: 100,
        dataIndex: 'account',
        key: 'account'
      },
      {
        title: '科目名称',
        width: 140,
        dataIndex: 'accountName',
        key: 'accountName'
      },
      {
        title: '值',
        width: 100,
        dataIndex: 'balance',
        key: 'balance'
      }
    ]
    return (
      <div className={style.modalData}>
        <Row>
          <div className={`table-group ${style.tableModal}`}>
            <Table
              rowKey={record => record.account}
              loading={tableLoading}
              columns={columns}
              dataSource={fundLIstResult || []}
              scroll={{ x: countTableScroll(columns) }}
              pagination={false}
            />
          </div>
        </Row>
      </div>
    )
  }
  render() {
    const { modalTitle } = this.props

    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        maskClosable={false}
        className={`${'operationModal'} ${'fullOrderModal'} `}
        footer={null}
        width='900px'
      >
        <Row gutter={16}>
          <div>{this.renderBomData()}</div>
        </Row>
      </Modal>
    )
  }
}
export default App



